import React from 'react';
import s from './BasketItem.module.css'
const BasketItem = (props) => {
    let money = props.price*props.piece;
    let plusPieceItem =()=>{
        props.plusPieceItem(props.i);
       
    }
    let setMinusItem =()=>{
        props.setMinusItem(props.i);
    }
    let setDelItem =()=>{
        props.setDelItem(props.i);
    }
    return (<div className={s.basketContent}>
        
       <div className={s.del}><span onClick={setDelItem}> удалить</span></div>
       <div className={s.title}><span>{props.title}</span></div>
       <div className={s.basketItemBlock}>
       <img src={props.photoUrl} />
       <div>
        <div className={s.itemPiece}>
            <div className={s.btn} onClick={setMinusItem}>-</div>
            <div className={s.itemPieceTablo}>{props.piece}</div>
            <div className={s.btn} onClick={plusPieceItem}>+</div>
        </div>
        <div className={s.papBlock}>
            <div className={s.price}>{money} &#8381;</div>
            <div className={s.pap}>{props.price} X {props.piece}</div>
        </div>
       </div></div>
    </div>)
}

export default BasketItem; 