import React from 'react';
import BookShopItem from '../bookShopItem/BookShopItem'
import s from './BookItem.module.css'

const BookItem = (props) => {
    return (
        <div className={s.bookContent}>
            {props.bookItem.map(el=>{if(el.id>props.typeBook[0] && el.id<props.typeBook[1])
                return(<BookShopItem {...el} key={el.id} i={el.id} setOpenBasket={props.setOpenBasket} setStoreItem={props.setStoreItem} />)
              
            }
            )}
        </div>
    )
}

export default BookItem; ;