import React from 'react';
import './App.css';
import { connect } from 'react-redux';
import {setOpenBasket, setStoreItem, plusPieceItem,setMinusItem,setSumIm, setSumPay, setDelItem,setDelAllItem,
  setMotherBook, setCookeryBook, setAnimalBook,setProgrammBook} from './redux/bookItem-reducer';

import BookItem from './components/bookContent/BookItem';
import ShopHeader from './components/shopHeader/ShopHeader';
import SideBar from './components/sideBar/SideBar';
import Basket from './components/basket/Basket';


const App =(props)=>{
  return (<>
  <ShopHeader setOpenBasket={props.setOpenBasket} basketItem={props.basketItem} />
  <SideBar setMotherBook={props.setMotherBook} setCookeryBook={props.setCookeryBook} 
  setAnimalBook={props.setAnimalBook} setProgrammBook={props.setProgrammBook} />
  { props.openBasket && <Basket {...props} /> }
    {!props.openBasket && <BookItem {...props} />}
  </>)
}

const mapStateToProps =(state)=> {
  return {
      bookItem: state.bookItem.bookItem,
      openBasket: state.bookItem.openBasket,
      basketItem: state.bookItem.basketItem,
      sumIm: state.bookItem.sumIm,
      sumPay: state.bookItem.sumPay,
      typeBook: state.bookItem.typeBook
  }
}

export default connect(mapStateToProps, {setOpenBasket, setStoreItem, plusPieceItem,setMinusItem,
   setSumIm, setSumPay, setDelItem, setDelAllItem,setMotherBook,setCookeryBook,setAnimalBook,setProgrammBook})(App);
