import React from 'react';
import s from './ShopHeader.module.css'
import basket from './../../image/basket1.png'

const ShopHeader = (props)=> {
    return (<div className={s.headerBlock}>
        <div className={s.headerContent}>
            <span className={s.title}>Книгомания</span>
            <div className={s.basket}  onClick={props.setOpenBasket}> 
                <div><img src={basket} /></div>
                <span>Корзина: {props.basketItem.length}</span></div>
        </div>
        
    </div>)
}

export default ShopHeader;