import React from 'react';
import s from './BookShopItem.module.css'


const BookShopItem =(props)=> {console.log(props)
    let setStoreItem =()=>{
        props.setStoreItem(props.id);
    }

    return (<>
    <div className={s.itemContent}>
     <div className={s.item}>
     <img src={props.photoUrl} className={s.photo} />
    <div className={s.bottomItem}><span className={s.priceBlock}>{props.price} &#8381;</span>  
    {props.basketAdd ? <span className={s.buttonAdd} onClick={props.setOpenBasket}>Добавлено</span>
    : <span className={s.button} onClick={setStoreItem}>В корзину</span>}</div>
    
    <p>{props.title}</p>
     </div>
    
    </div>
    </>)
}

export default BookShopItem;