import React from 'react';
import s from './SideBar.module.css'

const SideBar = (props)=> {
    return (<div className={s.navBlock}>
        <nav className={s.navMenu}>
            <span onClick={props.setMotherBook}>
                Для мам
            </span>
            <span onClick={props.setCookeryBook}>
                Кулинария
            </span>
            <span onClick={props.setAnimalBook}>
                Животные
            </span>
            <span onClick={props.setProgrammBook}>
                Программирование
            </span>
        </nav>
    </div>)
}

export default SideBar;