const OPEN_BASKET = 'OPEN_BASKET';
const ADDED_ITEM = 'ADDED_ITEM';
const PIECE_PLUS = 'PIECE_PLUS';
const MINUS_ITEM = 'MINUS_ITEM';
const DEL_ITEM = 'DEL_ITEM';
const DEL_ALL_ITEM = 'DEL_ALL_ITEM';
const SUM_IM = 'ONLINE_STORE_SUM_IM';
const SUM_PAY = 'ONLINE_STORE_SUM_PAY';
const MOTHER_BOOK = 'MOTHER_BOOK';
const COOKERY_BOOK = 'COOKERY_BOOK';
const ANIMAL_BOOK = 'ANIMAL_BOOK';
const PROGRAMM_BOOK = 'PROGRAMM_BOOK';

const initialState = {
    bookItem: [ 
        { id:1, basketAdd: false, title: 'Большая книга для мам', piece: 8, price: 450, photoUrl: photo1},
        { id:2, basketAdd: false, title: 'Мамы разные нужны', piece: 7, price: 320, photoUrl: photo2},
        { id:3, basketAdd: false, title: 'Книга российской мамы', piece: 9, price: 370, photoUrl: photo3},
        { id:4, basketAdd: false, title: 'Я скоро стану мамой', piece: 11, price: 410, photoUrl: photo4},
        { id:5, basketAdd: false, title: 'Я стала мамой', piece: 6, price: 630, photoUrl: photo5},
        { id:6, basketAdd: false, title: 'Детям о маме', piece: 5, price: 460, photoUrl: photo6},
        { id:7, basketAdd: false, title: 'ЗОЖигательная кулинария', piece: 7, price: 215, photoUrl: photo7},
        { id:8, basketAdd: false, title: 'КЕТО-кулинария', piece: 9, price: 290, photoUrl: photo8},
        { id:9, basketAdd: false, title: '1000 лучших рецептов', piece: 9, price: 370, photoUrl: photo9},
        { id:10, basketAdd: false, title: 'Охотничья кулинария', piece: 11, price: 410, photoUrl: photo10},
        { id:11, basketAdd: false, title: 'Уроки кулинарии', piece: 6, price: 630, photoUrl: photo11},
        { id:12, basketAdd: false, title: 'Новогодняя страна', piece: 7, price: 435, photoUrl: photo12},
        { id:13, basketAdd: false, title: 'Мир животных', piece: 7, price: 920, photoUrl: photo13},
        { id:14, basketAdd: false, title: 'Морские животные', piece: 9, price: 880, photoUrl: photo14},
        { id:15, basketAdd: false, title: 'Мои друзья кошки', piece: 5, price: 790, photoUrl: photo15},
        { id:16, basketAdd: false, title: 'Изучаем python', piece: 4, price: 1051, photoUrl: photo16},
        { id:17, basketAdd: false, title: 'Изучаем javaScript', piece: 5, price: 1550, photoUrl: photo17}
    ],
    openBasket: false,
    basketItem: [/* {id:1, piece:1},
        {id:3, piece:1},
        {id:6, piece:1} */

    ],
    sumIm: 0, //количество выбранных заказов в магазине
    sumPay: 0, // общая сумма денег на выбранные заказы
    typeBook: [0,7]
}

const bookItemReducer = (state=initialState, action) => {
    
    

    switch (action.type) {
        case OPEN_BASKET:
            return {...state, openBasket: !state.openBasket}
        case ADDED_ITEM: 
        for (let i=0;i<state.basketItem.length;i++){
            if(action.id===state.basketItem[i].id){
                return {...state}
            }
        } 
        let localState = {...state, ...state.bookItem, ...state.basketItem};
        localState.bookItem[action.id-1].basketAdd = true;
        localState.basketItem= [...state.basketItem, {id:action.id, piece:1}]
            return localState;
            case MINUS_ITEM:
                for (let i=0;i<state.basketItem.length;i++){
                    if(action.id===state.basketItem[i].id){
                        let a = [...state.basketItem];
                        let localState = {...state, ...state.basketItem};
                        if(a[i].piece>1){a[i].piece--}else a[i].piece=1;/* {a.splice(i,1);} */
                        localState.basketItem=a;
                        return localState;
                    }
                }  
                return {...state}
            case PIECE_PLUS:
                for (let i=0;i<state.basketItem.length;i++){
                    if(action.id===state.basketItem[i].id && state.basketItem[i].piece < state.bookItem[state.basketItem[i].id-1].piece){
                        let a = [...state.basketItem];
                        let localState = {...state, ...state.basketItem};
                        a[i].piece++
                        localState.basketItem=a;
                        return localState;
                    }
                } 
                return {...state }
                case SUM_IM:
                    let ddd = 0;
                    for (let i=0;i<state.basketItem.length;i++){
                        ddd += state.basketItem[i].piece;
                    }
                    return {...state, sumIm: ddd}
                case SUM_PAY:
                    let sum=0;
        for (let i=0;i<state.basketItem.length;i++){
           let a = state.bookItem[state.basketItem[i].id-1].price;
           let b = a * state.basketItem[i].piece;
            sum += b;}
                    return {...state, sumPay: sum}
                case DEL_ITEM:
                    for (let i=0;i<state.basketItem.length;i++){
                        if(action.id===state.basketItem[i].id){
                            let a = [...state.basketItem];
                            a.splice(i,1);
                            let localState = {...state};
        localState.bookItem[action.id-1].basketAdd = false;
        localState.basketItem= a;
            return localState;
                            /* return {...state,basketItem: a} */
                        }
                    }  
                    return {...state}
                    case DEL_ALL_ITEM:
                        if(state.basketItem.length>0) {
                            let a = [];
                           let localState = {...state, ...state.bookItem, ...state.basketItem};
                            for(let i=0;i<state.bookItem.length;i++){
                                if(localState.bookItem[i].basketAdd){
                                    localState.bookItem[i].basketAdd=false;
                                }localState.basketItem=a;
                            }
                            return localState;
                        } else {return {...state}}
                    case MOTHER_BOOK:                       
                            return {...state, ...state.typeBook, typeBook:[0,7]}
                    case COOKERY_BOOK:                       
                            return {...state, ...state.typeBook, typeBook:[6,13]}  
                    case ANIMAL_BOOK:                       
                            return {...state, ...state.typeBook, typeBook:[12,16]}  
                    case PROGRAMM_BOOK:                       
                            return {...state, ...state.typeBook, typeBook:[15,18]}                      
        default:
    return state;
}
}

export const setOpenBasket = () => ({type:OPEN_BASKET});
export const setStoreItem = (id)=>({type:ADDED_ITEM, id});
export const plusPieceItem = (id)=>({type:PIECE_PLUS, id});
export const setMinusItem = (id)=> ({type:MINUS_ITEM, id});
export const setDelItem = (id)=> ({type:DEL_ITEM, id});
export const setDelAllItem =()=>({type:DEL_ALL_ITEM});
export const setSumIm =()=>({type:SUM_IM});
export const setSumPay =()=>({type:SUM_PAY});
export const setMotherBook =()=>({type:MOTHER_BOOK})
export const setCookeryBook =()=>({type:COOKERY_BOOK})
export const setAnimalBook =()=>({type:ANIMAL_BOOK})
export const setProgrammBook =()=>({type:PROGRAMM_BOOK})

export default bookItemReducer;

