import React from 'react'
import s from './Basket.module.css'
import BasketItem from './BasketItem';

const Basket = (props) => {
    let zakaz = ()=> {
        alert(`Вы заказали ${props.sumIm} товаров на сумму ${props.sumPay} рублей`);
    }
    //общее количество товаров
    props.setSumIm();
    //общая сумма денег
    props.setSumPay();
    let a = props.basketItem.map(el=> 
        <BasketItem key={el.id} title ={props.bookItem[el.id-1].title } 
        photoUrl={props.bookItem[el.id-1].photoUrl}  price={props.bookItem[el.id-1].price} piece={el.piece}
        i={el.id} plusPieceItem={props.plusPieceItem} setMinusItem={props.setMinusItem}
         setDelItem={props.setDelItem} />)

    return (<div className={s.basketBlock}>     
        <div className={s.headerBasket}>
    <span><span className={s.basketTitle}>Корзина: </span>{props.basketItem.length}</span>
    <div>
    <div className={s.hideBasket} onClick={props.setOpenBasket} >Скрыть корзину</div>
    <span className={s.delItem} onClick={props.setDelAllItem} >удалить все товары</span>
    </div>
    
        </div>
         {props.basketItem.length ? a : <div className={s.emptyBasket}>Ваша корзина пуста</div>}
        <div className={s.footerBasketBlock}>
            <div className={s.basketSumIm}>
                 <div>{props.sumIm} товаров</div>
                 <div>Общая сумма.........................{props.sumPay} &#8381;</div>
            </div>
            <div className={s.footerBasketBtn} onClick={zakaz} >Оформить заказ</div>
        </div>  
    </div>)
}

export default Basket; 